@import "variables"

#head
	margin-top: calc(#{$header-height} + #{$side-margin})
	position: relative

	img
		width: 32vw
		height: 32vw
		margin-left: 20px
		object-fit: cover
		-webkit-transform: scaleX(-1)
		transform: scaleX(-1)

		@media (max-width: $mobile-breakpoint)
			width: 40vw
			height: 40vw
			border-radius: 20vw

	h1, h2
		position: absolute
		color: $primary-color

	h1
		top: 3vw
		left: 35vw
		font-size: 7vw

		@media (max-width: $mobile-breakpoint)
			font-size: 10vw
			left: 50vw

	h2
		top: 14vw
		left: 38vw
		font-weight: lighter

		@media (max-width: $mobile-breakpoint)
			top: 40vw
			left: 10px
			font-size: 1.2em
			color: unset


a
	text-decoration: none


.card-list
	display: flex
	padding: 3rem
	overflow-x: scroll
	margin-top: 50px


.card-list::-webkit-scrollbar
	width: 10px
	height: 10px

.card-list::-webkit-scrollbar-thumb
	background: #201c29
	border-radius: 10px
	box-shadow: inset 2px 2px 2px hsla(0, 0%, 100%, .25), inset -2px -2px 2px rgba(0, 0, 0, .25)


.card-list::-webkit-scrollbar-track
	background: linear-gradient(90deg, #201c29, #201c29 1px, #17141d 0, #17141d)


.card
	height: 350px
	width: 400px
	min-width: 250px
	padding: 1.5rem
	border-radius: 16px
	background: $card-color
	box-shadow: -1rem 0 3rem $card-shadow-color
	display: flex
	flex-direction: column
	transition: .2s
	margin: 0
	scroll-snap-align: start
	clear: both
	position: relative

.card-list:not(.mobile)
	.card:focus-within ~ .card, .card:hover ~ .card
		transform: translateX(130px)


	.card:hover
		transform: translateY(-1rem)

.mobile .card-active
	transform: translateY(-1rem)

.mobile .card-active ~ .card
	transform: translateX(130px)


.card:not(:first-child)
	margin-left: -130px

.card:last-of-type
	margin-right: 130px

.card-header
	margin-bottom: 20px
	//writing-mode: vertical-lr
	//transform: rotate(180deg)
	//text-align: center

	p
		font-size: 14px
		margin: 0 0 1rem
		color: #7a7a7a


	h2
		font-size: 20px
		margin: .25rem 0 auto
		text-decoration: none
		color: $card-title-color
		border: 0
		display: inline-block


.card-text
	color: #7a7a7a
	margin-left: 10px
	//height: min-content
	//margin-top: auto
	margin-bottom: auto
